.profile-picture {
    overflow: hidden;
    object-fit:scale-down;
    display: block;
    margin-left: auto;
    margin-right: auto;

    width: 15%;
    height: 15%;
    display: block;
    margin: auto;
    border: 5px solid #272727;
    border-radius: 100%;
}

.profile-name{
    text-align: center;
    color: #FF652F;
    border-bottom: 1px solid black;
    padding: 0px 0px 4px;
}

.profile-description {
    text-align: center;
}
